/* ==========================================================================
   Utility classes for transitions.

   Adds transitions utilty classes for transform, opacity,
   and for the removing the transition duration.
   ========================================================================== */
.u-no-animation {
  transition-duration: 0s !important;
}

.u-move-transition {
  transition: transform 0.25s ease-out;
}

.u-move-to-origin {
  transform: translate3d(0, 0, 0);
}

.u-move-left {
  transform: translate3d(-100%, 0, 0);
}

.u-move-left-2x {
  transform: translate3d(-200%, 0, 0);
}

.u-move-left-3x {
  transform: translate3d(-300%, 0, 0);
}

.u-move-right {
  transform: translate3d(100%, 0, 0);
}

.u-move-up {
  transform: translate3d(0, -100%, 0);
}

.u-alpha-transition {
  transition: opacity 0.25s linear;
}

.u-alpha-100 {
  opacity: 1;
}

.u-alpha-0 {
  opacity: 0;
}

.u-max-height-transition {
  overflow: hidden;
  contain: paint;
  transition: max-height 0.2s ease-out;
}

/*
.u-max-height-default {
  This class is kept for documentation completeness.
  The actual max-height is set in the JavaScript,
  so that we know what the actual height of the content is for easing purposes.
}
*/
.u-max-height-zero {
  max-height: 0 !important;
}

.u-max-height-summary {
  /* The value set here should show 4 lines of text at our standard 16px
     base font size. The calculation comes from the following:
     88px = 16 * 5.5em.
     5.5em = base-line-height (22px) * 4 / base-font-size (16px)
  */
  max-height: 88px !important;
}

.a-link {
  border-bottom-width: 0;
}
.a-link .a-link__text {
  border-bottom-width: 1px;
  border-bottom-style: inherit;
  overflow-wrap: break-word;
}

.a-link--jump {
  font-weight: 500;
}
@media only all and (max-width: 37.5em) {
  .a-link--jump {
    position: relative;
    display: flex;
    align-items: center;
    gap: 0.3125rem;
    box-sizing: border-box;
    padding-top: 0.625em;
    padding-bottom: 0.625em;
    width: 100%;
  }
  .a-link--jump .cf-icon-svg--right {
    margin-left: auto;
  }
  .a-link--jump .a-link__text {
    border-bottom-width: 0;
    flex-shrink: 10;
  }
}

a.a-link--jump {
  font-weight: 500;
}
@media only all and (max-width: 37.5em) {
  a.a-link--jump {
    border-top-width: 1px;
    border-bottom-width: 1px;
  }
  a.a-link--jump:focus {
    outline: none;
  }
  a.a-link--jump:focus::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    outline: 1px dotted var(--gray);
    outline-offset: 2px;
  }
}

@media only all and (max-width: 37.5em) {
  li:has(.a-link--jump) + li:has(.a-link--jump) .a-link--jump {
    position: relative;
    border-top: none;
  }
  li:has(.a-link--jump) + li:has(.a-link--jump) a.a-link--jump:hover::before,
  li:has(.a-link--jump) + li:has(.a-link--jump) a.a-link--jump:focus::before {
    position: absolute;
    top: -1px;
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    border-top: 1px solid currentcolor;
  }
}