/*
.u-max-height-default {
  This class is kept for documentation completeness.
  The actual max-height is set in the JavaScript,
  so that we know what the actual height of the content is for easing purposes.
}
*/
.u-max-height-zero {
  max-height: 0 !important;
}

.u-max-height-summary {
  /* The value set here should show 4 lines of text at our standard 16px
     base font size. The calculation comes from the following:
     88px = 16 * 5.5em.
     5.5em = base-line-height (22px) * 4 / base-font-size (16px)
  */
  max-height: 88px !important;
}

.a-tag-filter {
  display: flex;
  gap: 0.625rem;
  border: 1px solid var(--teal);
  padding: 4px 6px;
  background-color: var(--teal-20);
  border-radius: 0.1875rem;
  color: var(--black);
  text-align: left;
  min-width: fit-content;
}
.a-tag-filter .cf-icon-svg {
  pointer-events: none;
  flex: none;
}
.a-tag-filter > label {
  display: contents;
  pointer-events: none;
}

button.a-tag-filter:hover {
  background-color: var(--teal-40);
  cursor: pointer;
}
button.a-tag-filter:focus {
  outline: 1px dotted var(--teal);
  outline-offset: 1px;
}
button.a-tag-filter:active {
  background-color: var(--teal);
}

a.a-tag-filter {
  line-height: normal;
  color: var(--black);
  border-color: var(--teal);
}
a.a-tag-filter:visited, a.a-tag-filter.visited {
  border-color: var(--teal);
  color: var(--black);
}
a.a-tag-filter:hover, a.a-tag-filter.hover {
  border-color: var(--teal);
  color: var(--black);
}
a.a-tag-filter:focus, a.a-tag-filter.focus {
  border-color: var(--teal);
  color: var(--black);
}
a.a-tag-filter:active, a.a-tag-filter.active {
  border-color: var(--teal);
  color: var(--black);
}

.a-tag-topic {
  display: flex;
  gap: 0.3125rem;
}

.a-tag-topic {
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--gold-80);
  letter-spacing: 1px;
}
.a-tag-topic .a-tag-topic__text {
  color: var(--gray);
  word-break: break-word;
}
@media only all and (max-width: 37.5em) {
  .a-tag-topic {
    position: relative;
    display: flex;
    align-items: center;
    gap: 0.3125rem;
    box-sizing: border-box;
    padding-top: 0.625em;
    padding-bottom: 0.625em;
    width: 100%;
  }
  .a-tag-topic .cf-icon-svg--right {
    margin-left: auto;
  }
  .a-tag-topic .a-tag-topic__text {
    border-bottom-width: 0;
    flex-shrink: 10;
  }
}

a.a-tag-topic {
  border-color: var(--gold-80);
  color: var(--gold-80);
  border-color: var(--gold-80);
}
a.a-tag-topic:visited, a.a-tag-topic.visited {
  border-color: var(--gold-80);
  color: var(--gold-80);
}
a.a-tag-topic:hover, a.a-tag-topic.hover {
  border-color: var(--gold-80);
  color: var(--gold-80);
}
a.a-tag-topic:focus, a.a-tag-topic.focus {
  border-color: var(--gold-80);
  color: var(--gold-80);
}
a.a-tag-topic:active, a.a-tag-topic.active {
  border-color: var(--gold-80);
  color: var(--gold-80);
}
a.a-tag-topic .a-tag-topic__text {
  border-bottom-color: var(--gold-80);
}
@media only all and (max-width: 37.5em) {
  a.a-tag-topic {
    border-top-width: 1px;
    border-bottom-width: 1px;
  }
  a.a-tag-topic:focus {
    outline: none;
  }
  a.a-tag-topic:focus::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    outline: 1px dotted var(--gray);
    outline-offset: 2px;
  }
}

@media only all and (max-width: 37.5em) {
  li:has(.a-tag-topic) + li:has(.a-tag-topic) .a-tag-topic {
    position: relative;
    border-top: none;
  }
  li:has(.a-tag-topic) + li:has(.a-tag-topic) a.a-tag-topic:hover::before,
  li:has(.a-tag-topic) + li:has(.a-tag-topic) a.a-tag-topic:focus::before {
    position: absolute;
    top: -1px;
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    border-top: 1px solid currentcolor;
  }
}
.a-tag-topic__bullet {
  font-size: 1rem;
  line-height: 1rem;
}

@media only all and (min-width: 37.5625em) {
  a.a-tag-topic__text {
    color: var(--gray);
    border-color: var(--gray);
  }
  a.a-tag-topic__text:visited, a.a-tag-topic__text.visited {
    border-color: var(--gray);
    color: var(--gray);
  }
  a.a-tag-topic__text:hover, a.a-tag-topic__text.hover {
    border-color: var(--gray);
    color: var(--gray);
  }
  a.a-tag-topic__text:focus, a.a-tag-topic__text.focus {
    border-color: var(--gray);
    color: var(--gray);
  }
  a.a-tag-topic__text:active, a.a-tag-topic__text.active {
    border-color: var(--gray);
    color: var(--gray);
  }
  a.a-tag-topic,
  a.a-tag-topic:visited,
  a.a-tag-topic:hover,
  a.a-tag-topic:focus,
  a.a-tag-topic:active {
    border-bottom: none;
    outline-offset: 1px;
  }
  a.a-tag-topic .a-tag-topic__text,
  a.a-tag-topic:visited .a-tag-topic__text,
  a.a-tag-topic:hover .a-tag-topic__text,
  a.a-tag-topic:focus .a-tag-topic__text,
  a.a-tag-topic:active .a-tag-topic__text {
    padding-bottom: 1px;
    border-bottom: 1px dotted var(--gold-80);
  }
  a.a-tag-topic:hover .a-tag-topic__text {
    border-bottom: 1px solid var(--gold-80);
  }
  a.a-tag-topic:focus {
    outline-color: var(--gray);
  }
  a.a-tag-topic:focus .a-tag-topic__text {
    border-bottom-style: solid !important;
  }
}
